<template>
    <div class="globlgl  globlgl--custom">
        <div class="title">
            <div class="title1">分机账号</div>
            <div class="item">分机账号： {{showCheck.account}}</div>
            <div class="item">外显号码： {{showCheck.explicitNumber}}</div>
            <div class="item">免打扰开关： 
                <el-switch v-model="showCheck.isDisturb" on-color="#13ce66" off-color="#ff4949" @change="changeDisturb"> </el-switch>
            </div>
        </div>
        <div class="title">
            <div class="title1">SIP账号</div>
            <div class="item">SIP服务器： {{showCheck.SIPServer}}</div>
            <div class="item">密码： {{showCheck.password}}</div>
        </div>
    </div>
</template>

<script>

  import {
      getExtension,
      setDisturb,
  }
    from '@/api/getData';
  import ElTableTreeColumn from '../base/table-tree/table-column.js';
 
  export default {
    data() {

        return {
            showCheck:{
                account:'',
                explicitNumber:'',
                isDisturb:false,
                SIPServer:'',
                password:'',
            }
        }
    },
    created() {
      this.getExtension();
    },
    components: {
      ElTableTreeColumn
    },
    methods: {
      async getExtension(){
        this.showCheck.account = this.$store.baseStore.getters.getUserInfo.username;
        try {
            const res = await getExtension({
                extNbr: this.showCheck.account,
            });
            if (res.resultCode == '0') {
                this.showCheck.explicitNumber = res.result.didNumber;
                if(res.result.dndState == 1){
                    this.showCheck.isDisturb = true;
                }
                this.showCheck.SIPServer = res.result.domain;
                this.showCheck.password = res.result.password;
            } else {
                throw new Error(res.message)
            }
        } catch (err) {
            console.log('获取数据失败', err);
        }
      },
      async changeDisturb(){
            try{
                const res = await setDisturb({
                    extNbr:this.showCheck.account,
                    isDisturb:this.showCheck.isDisturb,
                });
                if(res.resultCode == '0'){
                    this.$message({
                        type: 'success',
                        message: '设置成功!'
                    });
                } else {
                    throw new Error(res.message)
                }
            } catch (err) {
                console.log('获取数据失败', err);
            }
          
      }
    }
  }

</script>
<style scope lang="less">
  .globlgl {
    padding: 33px 0 0 26px;
    box-sizing: border-box;
    .float-l {
      float: left;
    }
    .float-r {
      float: right;
    }
    .m-t-25 {
      margin-top: 25px;
    }
    //table 改组建
    .el-table {
      width: 100%;
      height: 100%;
      border: 0;
      &:before,
      &:after {
        display: none;
      }
      .el-table__body-wrapper {
        overflow: hidden;
      }
      th,
      td,
      .cell {
        border: 0px;
        background-color: initial;
        padding: 0px;
      }
      .cell {
        padding-left: 10px;
      }
      tr {
        border-bottom: 1px solid #e6e6e6;
      }
      thead tr {
        border-bottom: 2px solid #e6e6e6;
      }
    }
    .w-500 {
      .el-dialog {
        width: 500px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
          .item{
            height:40px;
            line-height:40px;
          }
        }
      }
    }
    .w-1000 {
      .el-dialog {
        width: 1000px;
        .el-dialog__body {
          max-height: 500px;
          overflow-y: auto;
        }
      }
    }
    .el-button--text {
      color: #00a8d7;
    }
    .btns {
      .btn {
        width: 80px;
        height: 36px;
        border: 1px solid #e6e6e6;
        color: #454545;
      }
      .float-r {
        float: right;
      }
    }
    .tables {
      .table {
        border: 0;
        margin: 15px 0;
      }
    }
    .m-t-26 {
      margin-top: 26px;
    }
    .globlgl-type {
      margin-right: 20px;
    }
    .nav {
      color: #99a9bf;
      cursor: pointer;
      transition: .3s;
      vertical-align: baseline;
      margin-right: 30px;
      &:hover {
        color: #48576a;
      }
    }
    .tree-grid {
      margin: 15px 0;
    } 
    .title{
        font-size:100%;
    }
    .item{
        margin: 30px;
        margin-left: 40px;
    }
    .title1{
        font-weight:bold;
        margin: 20px;
    }

  }

 
</style>





